"use client";

import * as React from "react";
import { Save, ExternalLink } from "lucide-react";

export default function AdminSettingsPage() {
    const [settings, setSettings] = React.useState({
        propertyName: "Villa Mosta",
        email: "info@villamosta.com",
        phone: "+356 99 123 456",
        address: "Mosta, Malta",
        checkInTime: "14:00",
        checkOutTime: "10:00",
        bookingUrl: "https://www.booking.com/hotel/mt/villa-mosta.html",
        heroVideoId: "9mg8FOQi6bI",
        heroVideoStart: 121,
        heroVideoEnd: 131,
    });
    const [saved, setSaved] = React.useState(false);

    const handleSave = () => {
        // In production, this would save to database
        setSaved(true);
        setTimeout(() => setSaved(false), 2000);
    };

    return (
        <>
            <header className="bg-white border-b border-stone-200 px-6 py-4 flex items-center justify-between">
                <div>
                    <h1 className="text-xl font-semibold text-stone-800">Settings</h1>
                    <p className="text-stone-500 text-sm">Configure your property</p>
                </div>
                <button
                    onClick={handleSave}
                    className="flex items-center gap-2 bg-amber-600 text-white px-4 py-2 rounded-lg hover:bg-amber-700 transition-colors"
                >
                    <Save className="w-4 h-4" />
                    <span>{saved ? "Saved!" : "Save Changes"}</span>
                </button>
            </header>

            <div className="p-6 max-w-2xl">
                {/* Property Info */}
                <section className="bg-white rounded-lg shadow-sm p-6 mb-6">
                    <h2 className="font-semibold text-stone-800 mb-4">Property Information</h2>
                    <div className="space-y-4">
                        <div>
                            <label className="block text-sm text-stone-600 mb-1">Property Name</label>
                            <input
                                type="text"
                                value={settings.propertyName}
                                onChange={(e) => setSettings({ ...settings, propertyName: e.target.value })}
                                className="w-full border border-stone-300 rounded-lg px-3 py-2"
                            />
                        </div>
                        <div className="grid grid-cols-2 gap-4">
                            <div>
                                <label className="block text-sm text-stone-600 mb-1">Email</label>
                                <input
                                    type="email"
                                    value={settings.email}
                                    onChange={(e) => setSettings({ ...settings, email: e.target.value })}
                                    className="w-full border border-stone-300 rounded-lg px-3 py-2"
                                />
                            </div>
                            <div>
                                <label className="block text-sm text-stone-600 mb-1">Phone</label>
                                <input
                                    type="tel"
                                    value={settings.phone}
                                    onChange={(e) => setSettings({ ...settings, phone: e.target.value })}
                                    className="w-full border border-stone-300 rounded-lg px-3 py-2"
                                />
                            </div>
                        </div>
                        <div>
                            <label className="block text-sm text-stone-600 mb-1">Address</label>
                            <input
                                type="text"
                                value={settings.address}
                                onChange={(e) => setSettings({ ...settings, address: e.target.value })}
                                className="w-full border border-stone-300 rounded-lg px-3 py-2"
                            />
                        </div>
                    </div>
                </section>

                {/* Check-in/out Times */}
                <section className="bg-white rounded-lg shadow-sm p-6 mb-6">
                    <h2 className="font-semibold text-stone-800 mb-4">Check-in / Check-out</h2>
                    <div className="grid grid-cols-2 gap-4">
                        <div>
                            <label className="block text-sm text-stone-600 mb-1">Check-in Time</label>
                            <input
                                type="time"
                                value={settings.checkInTime}
                                onChange={(e) => setSettings({ ...settings, checkInTime: e.target.value })}
                                className="w-full border border-stone-300 rounded-lg px-3 py-2"
                            />
                        </div>
                        <div>
                            <label className="block text-sm text-stone-600 mb-1">Check-out Time</label>
                            <input
                                type="time"
                                value={settings.checkOutTime}
                                onChange={(e) => setSettings({ ...settings, checkOutTime: e.target.value })}
                                className="w-full border border-stone-300 rounded-lg px-3 py-2"
                            />
                        </div>
                    </div>
                </section>

                {/* Hero Video Settings */}
                <section className="bg-white rounded-lg shadow-sm p-6 mb-6">
                    <h2 className="font-semibold text-stone-800 mb-4">Hero Video</h2>
                    <div className="space-y-4">
                        <div>
                            <label className="block text-sm text-stone-600 mb-1">YouTube Video ID</label>
                            <input
                                type="text"
                                value={settings.heroVideoId}
                                onChange={(e) => setSettings({ ...settings, heroVideoId: e.target.value })}
                                className="w-full border border-stone-300 rounded-lg px-3 py-2"
                                placeholder="e.g. 9mg8FOQi6bI"
                            />
                        </div>
                        <div className="grid grid-cols-2 gap-4">
                            <div>
                                <label className="block text-sm text-stone-600 mb-1">Start Time (seconds)</label>
                                <input
                                    type="number"
                                    value={settings.heroVideoStart}
                                    onChange={(e) => setSettings({ ...settings, heroVideoStart: Number(e.target.value) })}
                                    className="w-full border border-stone-300 rounded-lg px-3 py-2"
                                />
                            </div>
                            <div>
                                <label className="block text-sm text-stone-600 mb-1">End Time (seconds)</label>
                                <input
                                    type="number"
                                    value={settings.heroVideoEnd}
                                    onChange={(e) => setSettings({ ...settings, heroVideoEnd: Number(e.target.value) })}
                                    className="w-full border border-stone-300 rounded-lg px-3 py-2"
                                />
                            </div>
                        </div>
                    </div>
                </section>

                {/* External Links */}
                <section className="bg-white rounded-lg shadow-sm p-6">
                    <h2 className="font-semibold text-stone-800 mb-4">Booking Channels</h2>
                    <div>
                        <label className="block text-sm text-stone-600 mb-1">Booking.com URL</label>
                        <div className="flex gap-2">
                            <input
                                type="url"
                                value={settings.bookingUrl}
                                onChange={(e) => setSettings({ ...settings, bookingUrl: e.target.value })}
                                className="flex-1 border border-stone-300 rounded-lg px-3 py-2"
                            />
                            <a
                                href={settings.bookingUrl}
                                target="_blank"
                                rel="noopener noreferrer"
                                className="flex items-center gap-2 border border-stone-300 px-4 py-2 rounded-lg hover:bg-stone-50"
                            >
                                <ExternalLink className="w-4 h-4" />
                            </a>
                        </div>
                    </div>
                </section>

                <p className="mt-6 text-stone-500 text-sm">
                    Note: Settings are saved locally. Connect a database for persistent storage across sessions.
                </p>
            </div>
        </>
    );
}
